body {
    font-family: system-ui;
}

main {
    padding: 2em 0;
}

footer {
    margin: 2em 0;
}

.gallery td {
    vertical-align: top;
    padding: 2em 0;
}

.gallery h2 {
    margin: 0;
}

.gallery canvas {
    border: 2px dotted;
    margin-left: 4em;
}

.config {
    bottom: 0;
    line-height: 1.5;
}

.config .property {
    display: inline-flex;
    align-items: center;
    gap: 1ch;
}

.config .value {
    font-weight: bold;
}
